import React, { Component } from 'react';
import RetroButton from '../retroButton/RetroButton'

import coin from '../../../../assets/icons/coin.png'
import endScreen from '../../../../assets/pantallas/endScreen.png'



//imagenes
import goimg from '../../../../assets/products/img/go.png'
import jsimg from '../../../../assets/products/img/js.png'
import pythonimg from '../../../../assets/products/img/python.png'
import htmlimg from '../../../../assets/products/img/html.png'
import cimg from '../../../../assets/products/img/c.png'
import javaimg from '../../../../assets/products/img/java.png'
import reactimg from '../../../../assets/products/img/react.png'

//gift
import gif from '../../../../assets/gifts/dance.gif'

//styled
import styled from 'styled-components'

//products
import Products from '../../../../components/Products/Porducts'
const techs =[ 
    {
        id:"java",
        img:javaimg,
        cost:"paid",
        level:"easy",

    },
    {
        id:"go",
        img:goimg,
        cost:"free",
        level:"hard",
        titulo:"Experimental Drawings for Picturebooks",
        autor:"by Stografo",
        descripcion:"Un proyecto para manejar tecnologia avanzada de programacion orientada a objetos",
        open:true,
        precio:5,
        descuento:55,
    },
]
const Product = styled.img`
width:90px;
height:90px;
margin:10px;
border-radius: 15%;
display: inline-block;
border:solid #000 1px;

`;


class Filter extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            show: <div> { techs.map( product => <> { this.all( product ) } </> ) } </div>,
            Mostrador:<Mostrador className="default">All</Mostrador>,
            easy:true,
            medium:true,
            hard:true,
            paid:true,
            free:true,
            product:[],
         }
    }
    all = () => {
        
        return (<Products level={"easy"} />)
        
    }

    hard = (product , state) => {
        let hard = state.hard
        if (hard) {
            if (product.level==="hard") {
             this.setState({Mostrador:<Mostrador className="hard">Hard</Mostrador>})
                return (<Product src ={ product.img } />)
         }            
    }  else {
        this.setState(current => ({hard:!current.hard}))
        if (product.level==="hard") {
            this.setState({Mostrador:<Mostrador className="hard">Hard</Mostrador>})
            return (<Product src ={ product.img } />)
        } 

    }

    }

    medium = (product , state) => {
        
        var free = state.free
        var paid = state.paid
        let medium = state.medium
        if (medium) {
            if (product.level==="medium") {
                this.setState({Mostrador:<Mostrador className="medium">Medium</Mostrador>})
                return (<Product src ={ product.img } />)
            }            
        }  else {
            this.setState(current => ({medium:!current.medium}))
            if (product.level==="medium") {
                this.setState({Mostrador:<Mostrador className="medium">Medium</Mostrador>})
                return (<Product src ={ product.img } />)
            } 

        }

    }

    easy = (product , state) => {
        
        var free = state.free
        var paid = state.paid
        var easy = state.easy
        var medium = state.medium
        var hard = state.hard
      
            if (easy&&!medium&&!hard) {

                if (product.level==="easy") {
                    this.setState({Mostrador:<Mostrador className="easy">Easy</Mostrador>})
                    return (<Product src ={ product.img } />)
                 
                }            
            } else {
                this.setState(current => ({easy:!current.easy}))
                if (product.level==="easy") {
                    this.setState({Mostrador:<Mostrador className="easy">Easy</Mostrador>})
                    return (<Product src ={ product.img } />)
                 
                } 
            }

    }

    free = (product , state) => {

        let free    = state.free
        let easy    = state.easy
        let medium  = state.medium
        let hard    = state.hard
        if(!free){
            if (product.cost==="free") {
                this.setState({Mostrador:<Mostrador className="free"> Free </Mostrador>})
                return (<Product src ={ product.img } />)
            }

        } else {

            if (free) {
                if (easy&&!medium&&!hard) {
                    if ( product.level==="easy"&& product.cost==="free") {
                        this.setState({Mostrador:<Mostrador className="free"> Easy </Mostrador>})
                        return (<Product src ={ product.img } />)
                    }
        
                } else {
                    if (!easy&&medium&&!hard) {
                        if ( product.level==="medium"&& product.cost==="free") {
                            this.setState({Mostrador:<Mostrador className="free"> Medium </Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
                    } else {
                        if(!easy&&!medium&&hard){
                            if ( product.level==="hard"&& product.cost==="free") {
                                this.setState({Mostrador:<Mostrador className="free"> Hard </Mostrador>})
                                return (<Product src ={ product.img } />)
                            }
        
                        } else {
                            if (product.cost==="free") {
                                this.setState({Mostrador:<Mostrador className="free"> Free </Mostrador>})
                                return (<Product src ={ product.img } />)
                            }
                            
                        }
                    
        
                    }
        
                }
            
        
            } else {
                this.setState(current => ({free:!current.free}))
                if (free) {
                    if (easy&&!medium&&!hard) {
                        if ( product.level==="easy"&& product.cost==="free") {
                            this.setState({Mostrador:<Mostrador className="free" > Easy </Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
            
                    } else {
                        if (!easy&&medium&&!hard) {
                            if ( product.level==="medium"&& product.cost==="free") {
                                this.setState({Mostrador:<Mostrador className="free"> Medium </Mostrador>})
                                return (<Product src ={ product.img } />)
                            }
                        } else {
                            if(!easy&&!medium&&hard){
                                if ( product.level==="hard"&& product.cost==="free") {
                                    this.setState({Mostrador:<Mostrador className="free"> Hard </Mostrador>})
                                    return (<Product src ={ product.img } />)
                                }
            
                            } else {
                                if (product.cost==="free") {
                                    this.setState({Mostrador:<Mostrador className="free"> Free</Mostrador>})
                                    return (<Product src ={ product.img } />)
                                }
                                
                            }
                        
            
                        }
            
                    }
                
            
                } 
            }
        }
      
     
    }

    paid = (product , state) => {
        let paid = state.paid
        let easy = state.easy
        let medium = state.medium
        let hard = state.hard
        let free = state.free
        if(!paid){
            if (product.cost==="paid") {
                this.setState({Mostrador:<Mostrador className="paid"> Paid </Mostrador>})
                return (<Product src ={ product.img } />)
            }

        } else {

            if (paid&&!medium&&!hard) {
                if (easy) {
                    if ( product.level==="easy"&& product.cost==="paid") {
                        this.setState({Mostrador:<Mostrador className="paid"> Easy </Mostrador>})
                        return (<Product src ={ product.img } />)
                    }
        
                } else {
                    if (medium) {
                        if ( product.level==="medium"&& product.cost==="paid") {
                            this.setState({Mostrador:<Mostrador className="paid"> Medium </Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
                    } else {
                        if(hard){
                            if ( product.level==="hard"&& product.cost==="paid") {
                                this.setState({Mostrador:<Mostrador className="paid"> Hard </Mostrador>})
                                return (<Product src ={ product.img } />)
                            }
        
                        } else {
                            if (product.cost==="paid") {
                                this.setState({Mostrador:<Mostrador className="paid"> Paid </Mostrador>})
                                return (<Product src ={ product.img } />)
                            }
                            
                        }
                    
        
                    }
        
                }
            
        
            } else {
                this.setState(current => ({paid:!current.paid}))
                if (paid) {
                    if (easy&&!medium&&!hard) {
                        if ( product.level==="easy"&& product.cost==="paid") {
                            this.setState({Mostrador:<Mostrador className="paid" > Easy </Mostrador>})
                            return (<Product src ={ product.img } />)
                        }
            
                    } else {
                        if (!easy&&medium&&!hard) {
                            if ( product.level==="medium"&& product.cost==="paid") {
                                this.setState({Mostrador:<Mostrador className="paid"> Medium </Mostrador>})
                                return (<Product src ={ product.img } />)
                            }
                        } else {
                            if(!easy&&!medium&&hard){
                                if ( product.level==="hard"&& product.cost==="paid") {
                                    this.setState({Mostrador:<Mostrador className="paid"> Hard </Mostrador>})
                                    return (<Product src ={ product.img } />)
                                }
            
                            } else {
                                if (product.cost==="paid") {
                                    this.setState({Mostrador:<Mostrador className="paid"> Paid </Mostrador>})
                                    return (<Product src ={ product.img } />)
                                }
                                
                            }
                        
            
                        }
            
                    }
                
            
                } 
            }
        }
        
        
        
    }

   

    show = ( type ) =>{
        
        switch ( type ) {
            
            case "all":
                this.setState({show:<div>{ techs.map( aux => <> { this.all( aux ) } </> ) } </div>} )
                this.setState({Mostrador:<Mostrador className="default">All</Mostrador>})
                this.setState({easy:false})
                this.setState({medium:false})
                this.setState({hard:false})
                this.setState({free:false})
                this.setState({paid:false})
                break;

            case "easy":
                this.setState({show:<div>{ techs.map( aux => <> { this.easy( aux , this.state) } </> ) } </div>} )
                this.setState({easy:true})
                this.setState({medium:false})
                this.setState({hard:false})
                break;

            case "medium":
                this.setState({show:<div>{ techs.map( aux => <> { this.medium( aux , this.state) } </> ) } </div>} )
                this.setState({easy:false})
                this.setState({medium:true})
                this.setState({hard:false})
                break;
            case "hard":
                this.setState({show:<div>{ techs.map( aux => <> { this.hard( aux , this.state) } </> ) } </div>} )
                this.setState({easy:false})
                this.setState({medium:false})
                this.setState({hard:true})
                break;
            
            case "free":
                this.setState({show:<div>{ techs.map( aux => <> { this.free( aux , this.state) } </> ) } </div>} )
                this.setState({free:true})
                this.setState({paid:false})
                this.setState({easy:false})
                this.setState({medium:false})
                this.setState({hard:false})
                break;

            case "paid":
                this.setState({show:<div>{ techs.map( aux => <> { this.paid( aux , this.state) } </> ) } </div>} )
                this.setState({free:false})
                this.setState({paid:true})
                this.setState({easy:false})
                this.setState({medium:false})
                this.setState({hard:false})
                break;
        
            default:
                break;
        }
        
           
    }

    wardEasy   = () =>{ this.show("easy")  }
    wardMedium = () =>{ this.show("medium")}
    wardHard   = () =>{ this.show("hard")  }
    wardFree   = () =>{ this.show("free")  }
    wardPaid   = () =>{ this.show("paid")  }
    wardAll    = () =>{ this.show("all")   }


    render() { 
        const buttons = {

            easy:   <Button onClick     = {this.wardEasy}   > <RetroButton text="Easy"/>   </Button>,

            medium: <Button onClick     = {this.wardMedium} > <RetroButton text="Medium"/> </Button>,
 
            hard:   <Button onClick     = {this.wardHard}   > <RetroButton text="Hard"/>   </Button>,
 
            free:   <Free onClick = {this.wardFree}   > <RetroButton text="Free"/>   </Free>,

            paid:   <Paid onClick = {this.wardPaid}   > <RetroButton text="Paid"/>   </Paid>,

            all:    <All onClick     = {this.wardAll}    > <RetroButton text="All"/>  </All>,

        }
        

        return ( 

            <Page>
                 <Title> Proyects </Title>
                 <Hr><hr></hr></Hr>
                 
                    <Level>    
                        {buttons.easy}
                        {buttons.medium}
                        {buttons.hard} 
                        {buttons.all}
                    </Level>
                    
                    <DivMostrador>
                        {this.state.Mostrador}
                    </DivMostrador>

                    <InfoDiv>
                        <img className="gif" src={gif}/>
                    </InfoDiv>

                    <FreePaid>
                        {buttons.free}
                        {buttons.paid}  
                    </FreePaid>
                 
                    <DivProducts>
                
                
                    </DivProducts>
                
                

            </Page>

         );

    }

}
 
export default Filter;



