import React from 'react'
import './LogoNavbar.css' 
import Logo from '../../../assets/branging/logo.svg'
import { Link } from 'react-router-dom'

function reload(){
    window.location.reload()
}

function LogoNavbar(){
    return(
        <div onClick={reload}>
             <Link className="a-home_link" to="/"> 
            <img className="img-home-link" src={Logo} alt="CodeStografo svg logo"/> 

         </Link>

        </div>
       
    )
}
export default LogoNavbar
